.home {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-top: 2px solid black;
  border-bottom: 2px solid black;
  perspective: 200px;
}
.image_container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: flex-start;
  width: 196.5vh;
  height: 107vh;
  transition: 0.2s;
  transform-style: preserve-3d;
}
.image,
.imageS {
  position: relative;
  width: 23vh;
  margin: 2.5vh;
  overflow: hidden;
  cursor: pointer;
}

.imageS {
  flex-basis: 23vh;
  transition: 0.5s 0.05s;
}
.image {
  flex-basis: 34.5vh;
  transition: 0.5s;
}
.image_container div:nth-of-type(1) {
  animation: falling 6s 0.6s ease-out backwards;
}
.image_container div:nth-of-type(2) {
  animation: falling 6s 0.2s ease-out backwards;
}
.image_container div:nth-of-type(3) {
  animation: falling 6s 0.7s ease-out backwards;
}
.image_container div:nth-of-type(4) {
  animation: falling 6s 0.5s ease-out backwards;
}
.image_container div:nth-of-type(5) {
  animation: falling 6s 0.1s ease-out backwards;
}
.image_container div:nth-of-type(6) {
  animation: falling 6s 0.8s ease-out backwards;
}
.image_container div:nth-of-type(7) {
  animation: falling 6s ease-out backwards;
}
.image_container div:nth-of-type(8) {
  animation: falling 6s 1s ease-out backwards;
}
.image_container div:nth-of-type(9) {
  animation: falling 6s 0.3s ease-out backwards;
}
.image_container div:nth-of-type(10) {
  animation: falling 6s 0.9s ease-out backwards;
}
.image_container div:nth-of-type(11) {
  animation: falling 6s 0.4s ease-out backwards;
}
.image_container div:nth-of-type(12) {
  animation: falling 6s 0.6s ease-out backwards;
}
.image_container div:nth-of-type(13) {
  animation: falling 6s 0.2s ease-out backwards;
}
.image_container div:nth-of-type(14) {
  animation: falling 6s 0.7s ease-out backwards;
}
.image_container div:nth-of-type(15) {
  animation: falling 6s 0.5s ease-out backwards;
}
.image_container div:nth-of-type(16) {
  animation: falling 6s 0.1s ease-out backwards;
}
.image_container div:nth-of-type(17) {
  animation: falling 6s 0.8s ease-out backwards;
}
.image_container div:nth-of-type(18) {
  animation: falling 6s ease-out backwards;
}
.image_container div:nth-of-type(19) {
  animation: falling 6s 1s ease-out backwards;
}
.image_container div:nth-of-type(20) {
  animation: falling 6s 0.3s ease-out backwards;
}
.image_container div:nth-of-type(21) {
  animation: falling 6s 0.9s ease-out backwards;
}

@keyframes falling {
  0% {
    transform: translateZ(-500px);
    opacity: 0;
  }
  10% {
    transform: translateZ(-500px);
    /* opacity: 100%; */
  }
  40% {
    transform: translateZ(-20px);
  }
  100% {
    transform: translateZ(0);
    opacity: 100;
  }
}
.imageS img,
.image img {
  height: 100%;
  transition: 1s;
}
.imageS:hover,
.image:hover {
  opacity: 0.8;
}
